<template>
  <section class="register">
    <header>欢迎注册</header>
    <ul class="input-box">
      <li class="item username">
        <span class="iconfont">&#xe631;</span>
        <input v-model="username" type="text" placeholder="用户名">
        <span
          @click="clearUsernameValue"
          class="iconfont cancel"
        >
        &#xe621;
        </span>
      </li>
      <li class="item email">
        <span class="iconfont">&#xe8c9;</span>
        <input v-model="email" type="text" placeholder="邮箱">
        <span
          @click="clearEmailValue"
          class="iconfont cancel"
        >
        &#xe621;
        </span>
      </li>
      <li class="item password">
        <span class="iconfont">&#xe654;</span>
        <input v-model="password" type="password" placeholder="密码">
        <span
          @click="clearPasswordValue"
          class="iconfont cancel">
        &#xe621;
        </span>
      </li>
      <li class="item confirmPassword">
        <span class="iconfont">&#xe654;</span>
        <input v-model="confirmPassword" type="password" placeholder="确认密码">
        <span
          @click="clearConfirmPasswordValue"
          class="iconfont cancel">
        &#xe621;
        </span>
      </li>
    </ul>
    <div @click="getRegister" class="submit">注册</div>
  </section>
</template>

<script>
import { RegisterValidator } from '../../model/validator.js'
import { Message } from 'element-ui'
export default {
  name: 'Register',
  data () {
    return {
      username: '',
      email: '',
      password: '',
      confirmPassword: '',
      cancelBlock: false
    }
  },
  methods: {
    clearUsernameValue () {
      this.username = ''
    },
    clearEmailValue () {
      this.email = ''
    },
    clearPasswordValue () {
      this.password = ''
    },
    clearConfirmPasswordValue () {
      this.confirmPassword = ''
    },
    getRegister () {
      // 发送请求之前验证
      if (RegisterValidator.checkUsername(this.username) && RegisterValidator.checkEmail(this.email) && RegisterValidator.checkPassword(this.password) && RegisterValidator.checkPassword(this.confirmPassword) && RegisterValidator.checkConfirmPassword(this.password, this.confirmPassword)) {
        this.postAxios('v1/user/register', {
          username: this.username,
          email: this.email,
          password: this.password,
          confirmPassword: this.confirmPassword
        })
          .then(res => {
            console.log(res)
            if (res.status === 200 && res.data.errorCode === 0) {
              Message({
                message: res.data.message,
                type: 'success'
              })
              this.$router.push({name: 'Login'})
            }
          })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .register{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 240px;
    height: 260px;
    padding: 20px 30px;
    border: 1px solid #dfdfdf;
    border-radius: 5px;
    background-color: #fff;
    header{
      position: relative;
      width: 240px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #f40;
      &:after{
        content: "";
        display: block;
        position: absolute;
        top: 40px;
        left: 0px;
        width: 240px;
        height: 1px;
        background-color: #FFE4B5;
      }
    }
    .input-box{
      width: 240px;
      height: 150px;
      margin-top: 30px;
      .item{
        box-sizing: border-box;
        width: 240px;
        height: 30px;
        border: 1px solid #FFE4B5;
        color: #666;
        display: flex;
        flex-direction: row;
        align-items: center;
        span{
          color: #999!important;
          margin-left: 4px;
        }
        input{
          margin-left: 10px;
          border: 0;
          width: 180px;
          color: #666;
        }
        &:nth-of-type(n+2){
          margin-top: 10px;
        }
        .cancel{
          cursor: pointer;
        }
      }
    }
    .submit{
      width: 240px;
      height: 30px;
      line-height: 30px;
      margin-top: 20px;
      text-align: center;
      background-color: #f40;
      color: #fff;
      border-radius: 5px;
      cursor: pointer;
    }
  }
</style>



